import s from './SensorsStatus.module.css'
import greenEllipse from './../../../icons/content/greenEllipse.svg'
import redEllipse from './../../../icons/content/redEllipse.svg'

let SensorsStatus = (props) => {
    return (
        <div className={s.wrapper}>
            <div className={s.sensorsContent}>
                <h2></h2>
                <div style={{marginTop: '20px'}}> CO2 - MH-Z19B: {props.sensorsData.co2 ? '' : ' '} <img src={props.sensorsData.co2 ? greenEllipse : redEllipse}/></div>
                <div>  - DS18B20: {props.sensorsData.temp ? '' : ' '} <img src={props.sensorsData.temp ? greenEllipse : redEllipse}/></div>
                <div>  - PM Sensor SDS011: {props.sensorsData.dust ? '' : ' '} <img src={props.sensorsData.dust ? greenEllipse : redEllipse}/></div>
                <div>  - TGS2611: {props.sensorsData.methane ? '' : ' '} <img src={props.sensorsData.methane ? greenEllipse : redEllipse}/></div>
                <div>     - SHT30: {props.sensorsData.tempWet ? '' : ' '} <img src={props.sensorsData.tempWet ? greenEllipse : redEllipse}/></div>
                <div>     - BMP280: {props.sensorsData.tempPres ? '' : ' '} <img src={props.sensorsData.tempPres ? greenEllipse : redEllipse}/></div>
            </div>
        </div>
    );
}



export default SensorsStatus;
